class WFSvideo {
    constructor(config){
		this.config = config;
		this.defTypeVideo = ['video', 'youtube', 'vimeo', 'tiktok']
		this.def = {
			classVideo: 'wfsv-video',
			clSwitcher: 'wfsv-switcher',
			clActive: 'active',
			clFraction:"wfsv-fraction",
			clProgressbar: "wfsv-progressbar",
			splitYoutube: ['https://youtube.com/shorts/', 'https://youtu.be/'],
			splitTiktok: 'web_id=',
			splitVimeo: 'https://vimeo.com/',
			regex: /video\/.*?\?/g,
			regex2: /video\/(.*?)(?=\s|$)/g,
			clBeforeClose: 'wfsv-start-animate',
			clPagination: 'wfsv-pagination',
			defaultHtml:`<div class="wfsv-modal" id="wfsv-video">
							<div class="wfsv-holder-button">
								<div class="wfsv-holder-description">
									<strong class="wfsv-title"></strong>
								</div>
								<ul class="wfsv-list-button">
									<li>
										<a href="#" class="wfsv-icon wfsv-prev">
											<svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
												<path d="M11.165 6.91833L4.08331 14L11.165 21.0817" stroke="#292D32" stroke-width="2" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
												<path d="M23.9167 14H4.28168" stroke="#292D32" stroke-width="2" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
											</svg>
										</a>
									</li>
									<li>
										<a href="#" class="wfsv-icon wfsv-next">
											<svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
												<path d="M16.835 6.91833L23.9167 14L16.835 21.0817" stroke="#292D32" stroke-width="2" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
												<path d="M4.08331 14H23.7183" stroke="#292D32" stroke-width="2" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/>
											</svg> 
										</a>
									</li>
									<li>
										<a href="#" class="wfsv-icon wfsv-close">
											<svg width="28" height="28" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
												<g clip-path="url(#clip0_555_7451)">
													<path d="M21 7L7 21" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
													<path d="M7 7L21 21" stroke="black" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
												</g>
											</svg>
										</a>
									</li>
								</ul>
							</div>
							<div class="wfsv-holder-video"></div>
							<div class="wfsv-pagination"></div>
						</div>`
		}
		
		this.options = {
			conteiner: '.list-video',
			elements: '.open-modal-video',
			modalBox: '#wfsv-video',
			appendtDefaultHtml: false,
			videoBox: '.holder-video',
			openClass: 'open-video',
			title: true,
			type: true,
			titleClass: '.title',
			navigation: {
                nextBtn: ".next-video",
                prevBtn: ".prev-video",
				closeBtn: ".close-video",
                disableBtn: 'hedden-btn'
            },
			pagination: {
				start: false,
                el: '.wfs-pagination',
                type: 'switcher',
                htmlEl: '',
                clickable: true
            },
		}

		if(typeof config === 'object'){
			this.navigation = {...this.options.navigation, ...config.navigation};
			this.pagination = {...this.options.pagination, ...config.pagination};
            this.options = {...this.options, ...config}
        }

        this.init();
	}

	init(){
        this.#findAllElements();
		this.#checkElements();

		return this.options;
    }

	// -------------------------------------------------------------------------------------

	#findAllElements(){
		this.conteiner = document.querySelector(this.options.conteiner);
		if(this.conteiner != null){
			this.bodyElement = document.querySelector('body');
			this.elements = this.conteiner.querySelectorAll(this.options.elements);
			this.modalBox = document.querySelector(this.options.modalBox);

			if(!this.modalBox && this.options.appendtDefaultHtml){
				this.bodyElement.insertAdjacentHTML("beforeend", this.def.defaultHtml);
				this.modalBox = document.querySelector(this.options.modalBox);
			}
			if(this.modalBox){
				this.nextBtn = this.modalBox.querySelector(this.navigation.nextBtn);
				this.prevBtn = this.modalBox.querySelector(this.navigation.prevBtn);
				this.closeBtn = this.modalBox.querySelector(this.navigation.closeBtn);
				this.videoBox = this.modalBox.querySelector(this.options.videoBox);
				if(this.options.title) this.title = this.modalBox.querySelector(this.options.titleClass);
				if(this.pagination.start) this.paginationBox = this.modalBox.querySelector(this.pagination.el);
			}
		}
	}

	// -------------------------------------------------------------------------------------

	#checkElements(){
		this.arryCheck = [];
		this.flagButton = true;
		this.flagPagination = true;
		if(!this.options.type) {
			this.flagButton = false;
			this.flagPagination = false;
		}
		if(!this.flagButton || !this.pagination.start) this.flagPagination = false;
		if(!this.elements) this.arryCheck.push('Elements');
		if(!this.modalBox) this.arryCheck.push('Modal box');
		if(!this.flagButton && !this.nextBtn) this.arryCheck.push('Next button');
		if(!this.flagButton && !this.prevBtn) this.arryCheck.push('Prev button');
		if(!this.closeBtn) this.arryCheck.push('Close button');
		if(!this.videoBox) this.arryCheck.push('Video box');
		if(this.options.title && !this.title) this.arryCheck.push('Title element');
		if(this.flagPagination && !this.paginationBox) this.arryCheck.push('Pagination box');

		if(this.arryCheck.length > 0){
			console.log('Elements not found: ' + this.arryCheck);
		}else{
			this.#start();
		}
	}

	// -------------------------------------------------------------------------------------

	#start(){
		this.arryHrefLenth = this.elements.length;
		this.arryHrefcount = this.arryHrefLenth - 1;
		this.elementIndex = 0;

		this.#allElements();

		if(!this.flagButton){
			if(this.nextBtn) this.nextBtn.remove();
			if(this.prevBtn) this.prevBtn.remove();
		}else {
			this.handleNextClick = this.#handleNextClick.bind(this);
			this.handlePrevClick = this.#handlePrevClick.bind(this);
		}

		this.handleCloseClick = this.#closeModal.bind(this);
		this.handleTransitionend = this.#animationEnd.bind(this);
	}

	// -------------------------------------------------------------------------------------

	#animationEnd(e){
		if(this.bodyElement.classList.contains(this.def.clBeforeClose) && e.target === this.modalBox) {
			this.#destroyInsideAll();
		}
	}

	#createTransitionendListener(){
		this.modalBox.addEventListener('transitionend', this.handleTransitionend);
	}

	#removeTransitionendListener(){
		this.modalBox.removeEventListener('transitionend', this.handleTransitionend);
	}

	#allElements(){
		this.arryHref = [];
		this.arryTypeVideo = [];
		this.arryTitle = [];
		this.listeners = new Map();

		Array.from(this.elements).forEach((element, i) => {
			this.arryHref.push(element.getAttribute('href'));
			this.arryTypeVideo.push(element.getAttribute('data-type'));
			if(this.options.title) this.arryTitle.push(element.getAttribute('data-title'));

			const handleClick = (e) => this.#openModal(i, e);
			this.listeners.set(element, handleClick);
			element.addEventListener('click', handleClick);
		});
	}

	#destroyAllElements(){
		Array.from(this.elements).forEach((element, i) => {
			const handleClick = this.listeners.get(element);
			if (handleClick) {
				element.removeEventListener('click', handleClick);
				this.listeners.delete(element);
			}
		});
	}

	// -------------------------------------------------------------------------------------

	#openModal(i, e) {
		e.preventDefault();

		if(!this.bodyElement.classList.contains(this.options.openClass)){
			this.bodyElement.classList.add(this.options.openClass);
            this.elementIndex = i;
			this.#createEventClose();
			if(this.options.title) this.title.textContent = this.arryTitle[this.elementIndex];
			if(this.pagination.start && this.flagPagination && this.paginationBox){
				this.#createPagination();
				this.#rebuildPagination();
			}
			this.#createVideo();
			this.#createNextPrevEvent();
			if(this.flagButton) this.#disableBtn();
			this.#createTransitionendListener();
		}
	}

	// start CLOSE ---------------------------------------------------------------------------

	#closeModal(e) {
		e.preventDefault();
		
		if(this.bodyElement.classList.contains(this.options.openClass) && !this.bodyElement.classList.contains(this.def.clBeforeClose)){
			this.bodyElement.classList.add(this.def.clBeforeClose);
		}
	}

	#createEventClose (){
		this.closeBtn.addEventListener('click', this.handleCloseClick);
	}

	#removeEventClose (){
		this.closeBtn.removeEventListener('click', this.handleCloseClick);
	}

	// end CLOSE ---------------------------------------------------------------------------

	// -------------------------------------------------------------------------------------

	// start CREATE VIDEO ------------------------------------------------------------------

	#createVideo(){
		this.typeVideo = this.arryTypeVideo[this.elementIndex];
		this.href = this.arryHref[this.elementIndex];

		if(this.typeVideo == 'video'){
			this.htmlVideo = `<video id="${this.def.classVideo}" controls="" autoplay="true">
								<source id="source" src="${this.href}" type="video/mp4">
							  </video>`;
		}else{
			if(this.typeVideo == 'youtube'){
				for (let index = 0; index < this.def.splitYoutube.length; index++) {
					if(this.href.includes(this.def.splitYoutube[index])){
						this.href = this.href.replace(this.def.splitYoutube[index], '');
						this.htmlVideo = `<iframe id="${this.def.classVideo}" src="https://www.youtube-nocookie.com/embed/${this.href}&autoplay=1&loop=1" allow="autoplay; fullscreen" scrolling="auto"></iframe> `;
						index = this.def.splitYoutube.length;
					}
				}
			}

			if(this.typeVideo == 'vimeo'){
				this.href = this.href.replace(this.def.splitVimeo, '');
				this.htmlVideo = `<iframe id="${this.def.classVideo}" src="https://player.vimeo.com/video/${this.href}?autoplay=1&loop=1" frameborder="0" allow="autoplay; fullscreen" allowfullscreen></iframe> `;
			}

			if(this.typeVideo == 'tiktok'){
				if(this.href.includes('?')) this.href = this.href.match(this.def.regex).toString().slice(6, -1);
				else this.href = this.href.match(this.def.regex2).toString().slice(6);
				this.htmlVideo = `<iframe id="${this.def.classVideo}" src="https://www.tiktok.com/player/v1/${this.href}?&autoplay=1&description=1&loop=1&volume_control=1" allow="fullscreen" title="${this.arryTitle[this.elementIndex]}"></iframe>`;
			}
		}

		this.videoBox.innerHTML = this.htmlVideo;
		this.vodeoElement = this.modalBox.querySelector('#' + this.def.classVideo);
	}

	#removeVideo(){
		if(this.typeVideo == 'video') this.vodeoElement.pause();
		else this.vodeoElement.src = '';

		this.videoBox.innerHTML = '';
	}

	// end CREATE VIDEO --------------------------------------------------------------------

	// -------------------------------------------------------------------------------------

	// start NEXT and PREV -----------------------------------------------------------------

	#handleNextClick(e) {
        this.#nextPrevVideo(this.nextBtn, true, e);
    }

	#handlePrevClick(e) {
        this.#nextPrevVideo(this.prevBtn, false, e);
    }

	#createNextPrevEvent(){
		if(this.flagButton && this.options.type){
			this.nextBtn.addEventListener('click', this.handleNextClick);
			this.prevBtn.addEventListener('click', this.handlePrevClick);
		}
	}

	#destroyNextPrevEvent(){
		if(this.flagButton && this.options.type){
			this.nextBtn.removeEventListener('click', this.handleNextClick);
			this.prevBtn.removeEventListener('click', this.handlePrevClick);
		}
	}

	#nextPrevVideo(element, flag, e){
		e.preventDefault();

		if(!element.classList.contains(this.navigation.disableBtn)){	
			if(flag) this.elementIndex++;
			else this.elementIndex--;

			if(this.paginationBox && this.options.type) this.#rebuildPagination();
			if(this.flagButton) this.#disableBtn();
			this.#removeVideo();
			this.#createVideo();
		}
	}

	#disableBtn(){
		this.nextBtn.classList.remove(this.navigation.disableBtn);
		this.prevBtn.classList.remove(this.navigation.disableBtn);

		if(this.elementIndex == 0) this.prevBtn.classList.add(this.navigation.disableBtn);
		if(this.elementIndex == this.arryHrefcount) this.nextBtn.classList.add(this.navigation.disableBtn);
	}

	// end NEXT and PREV -------------------------------------------------------------------

	// -------------------------------------------------------------------------------------

	// start PAGINATION --------------------------------------------------------------------

	#rebuildPagination(){
		if(this.pagination.type == 'switcher') this.#rebuildSwitcher();
        if(this.pagination.type == 'progress') this.#rebuildProgress();
        if(this.pagination.type == 'fraction') this.#rebuildFraction();
	}

	#rebuildSwitcher(){
		Array.from(this.switcher).forEach((element, i) => {
			element.classList.remove(this.def.clActive)
		});

		this.switcher[this.elementIndex].classList.add(this.def.clActive);
	}

	#rebuildProgress(){
		this.progressbar.style.width = this.progressBoxW*(this.elementIndex + 1) + '%';
	}

	#rebuildFraction(){
		this.fractionActive.textContent = this.elementIndex + 1;
	}

	// -------------------------------------------------------------------------------------

	#createPagination(){
        if(this.pagination.type == 'switcher') this.#createSwitcher();
        if(this.pagination.type == 'progress') this.#createProgress();
        if(this.pagination.type == 'fraction') this.#createFraction();
    }

	#createSwitcher(){
		let switcherhtml = `<div class="${this.def.clSwitcher}">`;
		Array.from(this.elements).forEach((element, i) => {
			if(this.pagination.htmlEl.length > 0) switcherhtml += this.pagination.htmlEl;
			else switcherhtml += `<span></span>`;
		});
		switcherhtml += `</div>`;
		this.paginationBox.insertAdjacentHTML("afterbegin", switcherhtml);
		this.switcher = this.modalBox.querySelectorAll(`.${this.def.clSwitcher} > *`);

		if(this.pagination.clickable) {	
			this.listenersSwitcher = new Map();

            Array.from(this.switcher).forEach((switcher, i) => {
				const handleClick = (e) => this.#initEventSwitcher(i, e);
				this.listenersSwitcher.set(switcher, handleClick);
				switcher.addEventListener('click', handleClick);
            });
		}
	}

	#createProgress(){
		this.paginationBox.insertAdjacentHTML("afterbegin", `<div class="${this.def.clProgressbar}"><div class="${this.def.clProgressbar}-in"></div></div>`);
        this.progressbar = this.modalBox.querySelectorAll(`.${this.def.clProgressbar}-in`)[0];
		this.progressBoxW = 100/this.arryHrefLenth;
	}

	#createFraction(){
		this.paginationBox.insertAdjacentHTML("afterbegin", `
				<div class="${this.def.clFraction}">
					<div class="${this.def.clFraction}-active"></div>
					<div class="${this.def.clFraction}-total">${this.arryHrefLenth}</div>
				</div>`
		);

		this.fractionActive = this.modalBox.querySelectorAll(`.${this.def.clFraction}-active`)[0];
	}

	#initEventSwitcher(i, e){
		e.preventDefault();

		this.elementIndex = i;
		this.#rebuildPagination();
		if(this.flagButton) this.#disableBtn();
		this.#removeVideo();
		this.#createVideo();
	}

	// -------------------------------------------------------------------------------------

	#destroyPagination(){
		if(this.paginationBox && this.options.type){
			if(this.pagination.type == 'switcher'){
				Array.from(this.switcher).forEach((switcher, i) => {
					const handleClick = this.listenersSwitcher.get(switcher);
					if (handleClick) {
						switcher.removeEventListener('click', handleClick);
						this.listenersSwitcher.delete(switcher);
					}
				});
			}

			this.paginationBox.innerHTML = '';
		}
	}

	// end PAGINATION ----------------------------------------------------------------------

	// -------------------------------------------------------------------------------------

	#destroyInsideAll(){
		this.bodyElement.classList.remove(this.options.openClass);
		this.bodyElement.classList.remove(this.def.clBeforeClose);
		if(this.options.title) this.title.textContent = '';
		this.#removeVideo();
		this.#destroyPagination();
		this.#destroyNextPrevEvent();
		this.#removeEventClose();
		this.#removeTransitionendListener();
	}

	destroy(){
		this.#destroyAllElements();
	}
}